import { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { motion } from "framer-motion";
import { Link } from "react-router-dom"
import { Skeleton, notification } from "antd";
import "antd/dist/antd.min.css";
import "./font.css"

import axios from "axios";
import { AccountId, TransactionId, Client, TransactionReceipt, TransferTransaction, Hbar, PublicKey, CustomRoyaltyFee, Timestamp, TokenType, TokenCreateTransaction, CustomFixedFee, TokenMintTransaction } from "@hashgraph/sdk";



import Card from "./nftCard";
import BorrowArrow from "../../assets/images/Rectangle.png"
import Price from "../../assets/images/pricelogo.png";
import Sixsense from "../../assets/images/Sixsense.png";
import footerback from "../../assets/images/DOT1.png"


import { Tabs, Tab, Typography } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles"
import PropertiesCard from "./propertiesCard";

const useStyles = makeStyles((theme) => ({
  tabs: {
    minHeight: "300px"
  },
  indicator: {
    backgroundColor: 'red', // Customize the color of the tab underline here
    height: "6px"
  },
}));

const uri = `https://testnet.mirrornode.hedera.com/api/v1`;
const instance = axios.create({
  baseURL: uri,
});

const ownId = "0.0.5738066"
const client = Client.forTestnet();
client.setDefaultMaxTransactionFee(new Hbar(40));

const Buy = (props) => {
  const classes = useStyles();
  const [nft, setNft] = useState(null);
  const [value, setValue] = useState(1);
  const [propertiy, setPropertiy] = useState(4);
  useEffect(() => {
    props.setBuy(true)
    props.setLanding(true)
  })
  const { tokenId, name } = useParams();
  const { minted, setMinted, accountId, topic, hash, weaponNfts } = props;

  const nfts = weaponNfts

  //Get NFT details with accountId and tokenId from Database
  useEffect(() => {
    const arr = {
      name: name,
      tokenId: tokenId
    }

    axios.post("https://v3market.net:8000/users/getcid", arr).then(res => {
      console.log(res.data)
      setNft(res.data)
    })
  }, [name, tokenId])


  // handle Tab control
  const handleTabChange = (event, newValue) => {
    setValue(newValue);
  }

  // when finish the purchasing, send wallet nfts to backend 
  const Sendnft = (metadata) => {
    var arr = {
      accountId: accountId.toString(),
      metadata: metadata,
    };
    if (metadata.length > 0) {
      axios.post("https://v3market.net:8000/users/nftsave", arr).then((res) => {
        if (res.data === "success") alert("success");
        else if (res.data === "Already send") alert("Already send");
      });
    }
  };

  async function handleClickMint(cid) {
    console.log(cid);

    const transferHbarTx = await transferHbar(accountId, ownId);

    try {
      const transferHbarResponse = await sendTransaction(transferHbarTx);

      if (transferHbarResponse) {
        let accountInfo = await window.fetch("https://testnet.mirrornode.hedera.com/api/v1/accounts/" + accountId, { method: "GET" });
        accountInfo = await accountInfo.json();
        console.log(accountInfo);

        let key = await PublicKey.fromString(accountInfo.key.key);
        const Tx = TransactionId.generate(accountId);
        let customFees = []
        let nftCustomFee = await new CustomRoyaltyFee()
          .setNumerator(2)
          .setDenominator(5)
          .setFeeCollectorAccountId(accountId)
          .setFallbackFee(new CustomFixedFee().setHbarAmount(new Hbar(50)));

        console.log(nftCustomFee)
        customFees.push(nftCustomFee)
        const ninetyDaysSeconds = 60 * 60 * 24 * 90
        const secondsNow = Math.round(Date.now() / 1000)
        const timestamp = secondsNow + ninetyDaysSeconds
        const timestampObj = new Timestamp(timestamp, 0);
        console.log(timestampObj)

        let createTokenTx = await new TokenCreateTransaction()
          .setTransactionId(Tx)
          .setTokenName("KOMMANDO_WEAPONS")
          .setTokenSymbol("KMW")
          .setTokenType(TokenType.NonFungibleUnique)
          .setDecimals(0)
          .setInitialSupply(0)
          .setTreasuryAccountId(accountId)
          .setAutoRenewAccountId(accountId)
          .setCustomFees(customFees)
          .setAdminKey(key)
          .setSupplyKey(key)
          .setWipeKey(key)
          .setNodeAccountIds([new AccountId(3)])
          .setExpirationTime(timestampObj)
          .setAutoRenewPeriod(ninetyDaysSeconds)
          .freezeWith(client)

        try {
          const tokenCreateResponse = await sendTransaction(createTokenTx);
          if (tokenCreateResponse) {
            console.log(tokenCreateResponse.tokenId.toString())
            const tokenMintTx = await mintToken(tokenCreateResponse.tokenId.toString(), accountId, cid)
            console.log(tokenMintTx)
            try {
              const tokenMintResponse = await sendTransaction(tokenMintTx);

              if (tokenMintResponse) {
                console.log("success")
              }
            } catch (err) {
              console.log(err)
            }
          }
        } catch (err) {
          console.log(err);
        }
      }
    } catch (err) {
      console.log(err)
    }
  }

  //HBAR transfer
  function transferHbar(accountId, ownId) {
    const txID = TransactionId.generate(accountId);

    const transferTransaction = new TransferTransaction()
      .setTransactionId(txID)
      .addHbarTransfer(accountId, new Hbar(-nft.price))
      .addHbarTransfer(ownId, new Hbar(nft.price))
      .setNodeAccountIds([new AccountId(3)])
      .freeze(client)


    return transferTransaction;
  }


  // Send Transaction 
  async function sendTransaction(tx, sign = false) {
    let response, hashConnectTxBytes;
    console.log(tx)
    hashConnectTxBytes = sign ? (
      makeBytes(tx, accountId)
    ) : (
      tx.toBytes()
    );

    response = await hash?.sendTransaction(
      topic,
      {
        topic: topic,
        byteArray: hashConnectTxBytes,
        metadata: {
          accountToSign: accountId,
          returnTransaction: false,
        }
      }
    )

    if (response?.receipt) {
      return TransactionReceipt.fromBytes(
        response.receipt
      )
    } else {
      throw new Error("No transaction recipt found")
    }
  }

  //Mint TOken
  async function mintToken(tokenId, accountId, cids) {

    const txID = TransactionId.generate(accountId);

    const mintTx = await new TokenMintTransaction()
      .setTransactionId(txID)
      .setTokenId(tokenId)
      .setNodeAccountIds([new AccountId(3)])
      .setMetadata([Buffer.from(cids)])
      .freeze(client);

    return mintTx;
  }

  // get user sign
  async function makeBytes(trans, signingAcctId) {
    const transId = TransactionId.generate(signingAcctId);

    trans.setTransactionId(transId);
    trans.setNodeAccountIds([new AccountId(3)]);

    await trans.freeze();

    const transBytes = trans.toBytes();

    return transBytes
  }


  if (nft === null) {
    return <div></div>
  } else {
    return (
      <div>
        <motion.section
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ ease: "easeInOut", duration: 0.9, delay: 0.2 }}
          className="container mx-auto mt-7">
          <Link to="/"><img src={BorrowArrow} /></Link>

          <div className="grid lg:grid-cols-2 md:grid-cols-1 mt-6 sm:gap-4 sm:grid-cols-1 mb-20">
            <div className="flex justify-center items-center">
              <Card>
                <img
                  src={"https://ipfs.io/ipfs/" + nft.image}
                  alt="nft img"
                  className="shadow-2xl "
                  style={{ width: "300px", height: "450px", objectFit: "cover", clipPath: "polygon(0 0, 100% 0, 100% 90%, 90% 100%, 0 100%)" }}
                />
              </Card>
            </div>

            <div className="lg:w-3/4 px-10 sm:px-0">
              <div
                className="
                text-5xl
                pt-10
                font-semibold

                        "
                style={{ color: "black", fontFamily: "nftname" }}
              >
                {nft.name.toUpperCase()}
              </div>
              <div
                className="font-general-regular 
                          sm:text-left text-black
                          text-md
                          font-medium
                          pt-3
                        " style={{ fontFamily: "nftdescription", fontSize: "24px", lineHeight: "37px" }}>
                {nft.description.toUpperCase()}
              </div>
              <div
                className="font-general-regular 
                        text-left
                        text-white
                        text-sm
                        sm:text-xl
                        pt-10
                        font-bold
                        ">
                Price
              </div>
              <div className="flex h-20" >
                <img src={Price} className="mr-5" alt="image" />
                <h1 className="text-5xl font-semibold pt-3" style={{ color: "black", fontFamily: "nftname" }}>{nft.price}</h1>
              </div>
              <div className="  mt-10">
                <button
                  className=" sm:w-full h-10   font-general-medium 
                                      rounded-md shadow-sm text-md text-white w-full font-bold text-xl mr-10
                                    " style={{ backgroundColor: '#FF0000', fontFamily: "nftname" }}
                  onClick={() => handleClickMint(nft.cid)}
                >
                  PURCHASE
                </button>
                {/* <button
                  className=" sm:w-full h-10 font-general-medium 
                                      rounded-md shadow-sm text-md text-black w-full font-bold text-xl 
                                    "
                  style={{ border: "2px solid black" }}
                // onClick={() => buyNFT()} 
                >
                  SAVE
                </button> */}
              </div>
            </div>
          </div>

          <Tabs
            value={value}
            variant="fullWidth"
            onChange={handleTabChange}
            TabIndicatorProps={{
              className: classes.indicator,
            }}
          >
            <Tab value={1} label={<Typography variant="body1" style={{ fontWeight: 'bold', fontSize: '16px' }}>
              BUNDLE
            </Typography>} />
            <Tab value={2} label={<Typography variant="body1" style={{ fontWeight: 'bold', fontSize: '16px' }}>
              PROPERTIES
            </Typography>} />

          </Tabs>
          {value === 1 && <div className="grid grid-cols-1 lg:grid-cols-2 md:grid-cols-1 sm:gap-10 bg-black pt-16 pb-16 lg:pl-32 px-20" >
            <div className="sm:pb-1 pb-10">
              <div
                className="
                text-white
                text-3xl
                font-black
                        "  style={{ fontFamily: "font", fontWeight: "300px" }}>
                {nft.name.toUpperCase()}
              </div>
              <div
                className="font-general-regular 
                          sm:text-left text-white
                          text-white
                          text-md
                          sm:text-sm
                        " style={{ fontFamily: "revert-layer" }}>
                {nft.description.toUpperCase()}
              </div>
            </div>
            <div className="">
              <img src={Sixsense} className="w-80 sm:h-32 h-20" style={{ width: "300px", height: "120px" }} />
            </div>
          </div>}
          {value === 2 && <div className="grid sm:grid-cols-1 md:grid-cols-1 lg:grid-cols-2 p-10 " style={{ backgroundImage: `url(${footerback})`, backgroundColor: "black" }}>
            <div className="grid grid-cols-2 lg:grid-cols-2 xl:grid-cols-4 py-3 sm:gap-2 sm:grid-cols-4  pt-8 pb-8">
              {propertiy === 0 && (
                <>
                  <Skeleton.Button active size="large" shape="default" />
                  <Skeleton.Button active size="large" shape="default" />
                  <Skeleton.Button active size="large" shape="default" />
                  <Skeleton.Button active size="large" shape="default" />
                </>
              )}
              <PropertiesCard
                propertiy={"WeaponType"}
                value={nft.weaponType}
                key={1}
              />
              <PropertiesCard
                propertiy={"Control"}
                value={nft.Control}
                key={2}
              />
              <PropertiesCard
                propertiy={"RPM"}
                value={nft.RPM}
                key={3}
              />
              <PropertiesCard
                propertiy={"Range"}
                value={nft.Range}
                key={4}
              />

            </div>
            <div className="px-8 sm:px-30 md:px-40 lg:px-20 ">

              <div
                className="font-general-regular 
                      text-left 
                      text-white
                      text-sm
                      sm:text-xl
                      pt-10
                      font-bold
                      ">
                Details


              </div>
              <div
                className="grid md:grid-cols-2 lg:grid-cols-2 sm:grid-cols-2 grid-cols-2
                        ">
                <div
                  className="font-general-regular 
                      text-left 
                      text-white
                      text-xs
                      sm:text-xs
                      pt-3
                      font-bold
                      ">
                  Token Id :{" "}
                </div>
                <div
                  className="font-general-regular 
                      text-left 
                      text-white
                      text-xs
                      sm:text-xs
                      pt-3
                      font-bold
                      
                      ">
                  <Link
                    to={`https://hashscan.io/testnet/token/${tokenId}`}
                    className="text-blue-500
                        dark:text-blue-500
                        dark:hover:text-blue-300"
                    target="_blank"
                    rel="noreferrer">
                    {tokenId}
                  </Link>
                </div>
                <div
                  className="font-general-regular 
                      text-left 
                      text-white
                      text-xs
                      sm:text-xs
                      pt-3
                      font-bold
                      
                      ">
                  SerialNumber :{" "}
                </div>
                <div
                  className="font-general-regular 
                      text-left 
                      text-white
                      text-xs
                      sm:text-xs
                      pt-3
                      font-bold
                      
                      ">
                  {1}
                </div>
                <div
                  className="font-general-regular 
                      text-left 
                      text-white
                      text-xs
                      sm:text-xs
                      pt-3
                      font-bold
                      ">
                  Token Standard :
                </div>
                <div
                  className="font-general-regular 
                      text-left 
                      text-white
                      text-xs
                      sm:text-xs
                      pt-3
                      font-bold
                      ">
                  HTS
                </div>
                <div
                  className="font-general-regular 
                      text-left 
                      text-white
                      text-xs
                      sm:text-xs
                      pt-3
                      font-bold
                      ">
                  Blockchain :
                </div>
                <div
                  className="font-general-regular 
                      text-left 
                      text-white
                      text-xs
                      sm:text-xs
                      pt-3
                      font-bold
                      ">
                  Hedera
                </div>
              </div>
            </div>
          </div>}
        </motion.section>


      </div>

    );
  }

};

export default Buy;


