import { motion } from "framer-motion";
import axios from "axios"
import { useContext } from "react";
import { Link } from "react-router-dom";
import { HaschconnectContext } from "../../App";
import { AccountId, PrivateKey, Client, CustomRoyaltyFee, CustomFixedFee, Timestamp, TransactionId, TransactionReceipt, TransferTransaction, Hbar, TokenAssociateTransaction, TokenMintTransaction, PublicKey, TokenCreateTransaction, TokenType, TokenSupplyType } from "@hashgraph/sdk";
import { useEffect } from "react";


const tokenId = "0.0.5741000" // KOMMANDO_WEAPONS
const ownId = "0.0.5738066"
const clientId = "0.0.5738044"
const privateKey = "302e020100300506032b657004220420aece654cc43402e6fabe65a0d8ebfbd38532eb1c66b45fb0d29361755d5b73da"

const uri = `https://testnet.mirrornode.hedera.com/api/v1`;
const instance = axios.create({
  baseURL: uri,
});



const operatorId = AccountId.fromString(clientId);
const operatorKey = PrivateKey.fromString(privateKey);

const client = Client.forTestnet();
client.setDefaultMaxTransactionFee(new Hbar(40));

const NftSingle = ({ name, image, accountId, topic, cid, price }) => {
  const { hash, minted, setMinted, weaponNfts } = useContext(HaschconnectContext);
  let nfts = weaponNfts;


  // When user buy nft, automatically update useState of current nfts and backend server 
  const Sendnft = (metadata) => {
    var arr = {
      accountId: accountId.toString(),
      metadata: metadata,
    };
    if (metadata.length > 0) {
      axios.post("https://v3market.net:8000/users/nftsave", arr).then((res) => {
        if (res.data === "success") alert("success");
        else if (res.data === "Already send") alert("Already send");
      });
    }
  };


 
  // check this nft is already associated
  // async function isNFTAssociatedWithWallet(tokenId, accountId) {
  //   let check = false
  //   try {
  //     let balance = await instance.get(`balances?account.id=${accountId}`);
  //     const tokens = balance.data.balances[0].tokens;
  //     for (var i = 0; i < tokens.length; i++) {
  //       if (tokens[i].token_id === tokenId) check = true

  //     }
  //     return check
  //   } catch (err) {
  //   }
  // }


  // when user click the buy button >- buy.
  async function handleClickMint(cid) {
    console.log(cid);
    const transferHbarTx = await transferHbar(accountId, ownId);

    try {
      const transferHbarResponse = await sendTransaction(transferHbarTx);

      if (transferHbarResponse) {
        let accountInfo = await window.fetch("https://testnet.mirrornode.hedera.com/api/v1/accounts/" + accountId, { method: "GET" });
        accountInfo = await accountInfo.json();
        console.log(accountInfo);

        let key = await PublicKey.fromString(accountInfo.key.key);
        const Tx = TransactionId.generate(accountId);
        let customFees = []
        let nftCustomFee = await new CustomRoyaltyFee()
          .setNumerator(2)
          .setDenominator(5)
          .setFeeCollectorAccountId(accountId)
          .setFallbackFee(new CustomFixedFee().setHbarAmount(new Hbar(50)));

        console.log(nftCustomFee)
        customFees.push(nftCustomFee)
        const ninetyDaysSeconds = 60 * 60 * 24 * 90
        const secondsNow = Math.round(Date.now() / 1000)
        const timestamp = secondsNow + ninetyDaysSeconds
        const timestampObj = new Timestamp(timestamp, 0);
        console.log(timestampObj)

        let createTokenTx = await new TokenCreateTransaction()
          .setTransactionId(Tx)
          .setTokenName("KOMMANDO_WEAPONS")
          .setTokenSymbol("KMW")
          .setTokenType(TokenType.NonFungibleUnique)
          .setDecimals(0)
          .setInitialSupply(0)
          .setTreasuryAccountId(accountId)
          .setAutoRenewAccountId(accountId)
          .setCustomFees(customFees)
          .setAdminKey(key)
          .setSupplyKey(key)
          .setWipeKey(key)
          .setNodeAccountIds([new AccountId(3)])
          .setExpirationTime(timestampObj)
          .setAutoRenewPeriod(ninetyDaysSeconds)
          .freezeWith(client)

          try {
          const tokenCreateResponse = await sendTransaction(createTokenTx);
          if (tokenCreateResponse) {
            console.log(tokenCreateResponse.tokenId.toString())
            const tokenMintTx = await mintToken(tokenCreateResponse.tokenId.toString(), accountId, cid)
            console.log(tokenMintTx)
            try {
              const tokenMintResponse = await sendTransaction(tokenMintTx);

              if (tokenMintResponse) {
                console.log("success")
              }
            } catch (err) {
              console.log(err)
            }
          }
        } catch (err) {
          console.log(err);
        }
      }
    } catch (err) {
      console.log(err)
    }
  }

  async function mintToken(tokenId, accountId, cids) {

    const txID = TransactionId.generate(accountId);

    const mintTx = await new TokenMintTransaction()
      .setTransactionId(txID)
      .setTokenId(tokenId)
      .setNodeAccountIds([new AccountId(3)])
      .setMetadata([Buffer.from(cids)])
      .freeze(client);

    return mintTx;
  }


  // Send Transaction for hbar buyer to owner
  async function sendTransaction(tx, sign = false) {
    let response, hashConnectTxBytes;
    console.log(tx)
    hashConnectTxBytes = sign ? (
      makeBytes(tx, accountId)
    ) : (
      tx.toBytes()
    );

    response = await hash?.sendTransaction(
      topic,
      {
        topic: topic,
        byteArray: hashConnectTxBytes,
        metadata: {
          accountToSign: accountId,
          returnTransaction: false,
        }
      }
    )

    if (response?.receipt) {
      return TransactionReceipt.fromBytes(
        response.receipt
      )
    } else {
      throw new Error("No transaction recipt found")
    }
  }


  // get user sign
  async function makeBytes(trans, signingAcctId) {
    const transId = TransactionId.generate(signingAcctId);

    trans.setTransactionId(transId);
    trans.setNodeAccountIds([new AccountId(3)]);

    await trans.freeze();

    const transBytes = trans.toBytes();

    return transBytes
  }


  //HBAR transfer
  function transferHbar(accountId, ownId) {
    const txID = TransactionId.generate(accountId);

    const transferTransaction = new TransferTransaction()
      .setTransactionId(txID)
      .addHbarTransfer(accountId, new Hbar(-price))
      .addHbarTransfer(ownId, new Hbar(price))
      .setNodeAccountIds([new AccountId(3)])
      .freeze(client)


    return transferTransaction;
  }

  //NFT Associate
  // function associateNFT(accountId) {
  //   const txID = TransactionId.generate(accountId);
  //   const tokenIds = [tokenId];
  //   console.log(tokenIds)
  //   const associateTransaction = new TokenAssociateTransaction()
  //     .setTransactionId(txID)
  //     .setTokenIds(tokenIds)
  //     .setAccountId(accountId)
  //     .setNodeAccountIds([new AccountId(3)])
  //     .freeze();

  //   return associateTransaction;
  // }

  return (

    <motion.div
      initial={{ opacity: 0 }}
      animate={{ opacity: 1, delay: 1 }}
      transition={{
        ease: "easeInOut",
        duration: 0.7,
        delay: 0.15,
      }}
      style={{ border: "1px solid #313f4a", borderRadius: "10px", boxShadow: "2px 2px 5px #00000040", padding: "20px 15px", position: "relative" }}
    >
      <div
        className="bg-secondary-light cursor-pointer  
                        hover:shadow-xl mb-10 mr-4 ml-4 rounded-xl shadow-lg sm:mb-0 m-1"
      >
        <Link
          to={`/buy/` + tokenId + `/` + accountId + `/` + name}
          aria-label="Single Project"
        >
          <div className="mt-16">
            <img
              src={"https://ipfs.io/ipfs/" + image.slice("7")}
              className="2xl:h-72 h-36 lg:h-44 md:h-48 object-cover rounded-xl sm:h-62 w-full xl:h-56"
              alt="Single Project"
            />
          </div>
          <div className="px-4 py-2">
            <p
              className="dark:text-white font-bold font-general-medium mb-3 mt-3
                            md:text-xl text-center lg:text-2xl text-ternary-dark text-lg"
            >
              {name}
            </p>
            <div className="text-black text-center lg:text-xl mb-2">
              {price}HBAR
            </div>
          </div>
        </Link>
        {accountId ? (
          <motion.button
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            transition={{
              ease: "easeInOut",
              duration: 0.9,
              delay: 0.1,
            }}
            style={{
              width: "100%",
              borderRadius: "10px",
              display: "flex",
              fontSize: "18px",
              fontStyle: "normal",
              fontWeight: "600",
              lineHeight: "22px",
              padding: "10px 30px",
              textAlign: "center",
              textShadow: "0 0 4px #0006",
              backgroundColor: "#080810",
              alignItems: "center",
              justifyContent: "center"
            }}
            onClick={() => handleClickMint(cid)}
          >
            <motion.h1
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
              transition={{
                ease: "easeInOut",
                duration: 0.9,
                delay: 0.1,
              }}
              className="font-general-regular 
                            text-center text-white
                            font-black
                            mt-1  
                            "
            >
              MINT NOW
            </motion.h1>
          </motion.button>
        ) : (
          <></>
        )}

      </div>

    </motion.div>
  );
};

export default NftSingle;
